import React from 'react';
import ReactDOM from 'react-dom';
import './style.css';
import 'semantic-ui-css/semantic.min.css';
import { Container, Image, Menu} from 'semantic-ui-react';
import { NavLink } from 'react-router-dom';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';

class TopMenu extends React.Component {
  render() {
    return (
        <Menu borderless white className="topmenu">
          <Container>
            <Menu.Item as={NavLink} activeClassName="active" exact to="/about" key='add'><a>HKN18</a></Menu.Item>
            <Menu.Item position="right"><a>About</a></Menu.Item>
            <Menu.Item><a>Courses</a></Menu.Item>
          </Container>
        </Menu>
    )
  }
}

class Middle extends React.Component {
  render() {
    return (
        <Image fluid src="http://www.hardrock.com/cafes/honolulu/files/2384/Honolulu_Entrance.jpg"/>
    )
  }
}

class Footer extends React.Component {
  render() {
    const lowerStyle = { color: "black",textAlign: "center" };
    return (
        <Container>
          <p style={lowerStyle}>
            University of Hawai'i at Manoa<br/>
            Honolulu, HI 96822
          </p>
        </Container>
    )
  }
}

class HardRock extends React.Component {

  render() {
    return (
      <div>
        <TopMenu/>
        <Middle />
        <Footer />
      </div>
  );
  }
}

/** Top-level layout component for this application. Called in imports/startup/client/startup.jsx. */
class App extends React.Component {
  render() {
    return (
        <Router>
          <div>
            <Switch>
              <Route exact path="/" component={HardRock}/>
              <Route exact path="/about" component={Middle}/>
            </Switch>
          </div>
        </Router>
    );
  }
}

ReactDOM.render(<App/>, document.getElementById('root'));



